<template>
  <div id="app">
    <!-- 1.0 利用mint-ui的header组件实现整个系统的头部 -->
    <mt-header id="header" fixed title="离院评估">
      <!--不要返回按钮-->
      <!--<router-link to="" slot="left" id="goBack" v-show="isShow">
        <i class="mint-toast-icon mintui mintui-back" @click="goBackFn"></i>
      </router-link>-->
    </mt-header>

    <router-view id="contentBody"></router-view>

    <!-- 3.0 利用mui的tabbar组件实现整个系统的底部 -->
    <nav class="mui-bar mui-bar-tab">
      <router-link class="mui-tab-item" to="/">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
      </router-link>
      <router-link class="mui-tab-item" to="">
        <span class="mui-icon mui-icon-gear"></span>
        <span class="mui-tab-label">我的</span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  data:function(){
    return {
      isShow:false
    }
  },
  watch:{
    $route:function(now,old){     //监控路由变换，控制返回按钮的显示
      if(now.path=="/index" || now.path=="/"){
        this.isShow=false;
      } else{
        this.isShow=true;
      }
    }
  },
  methods:{
    goBackFn:function(){
      this.$router.go(-1);//返回上一层
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#contentBody{
  margin-top:60px;
  margin-bottom:60px;
}
#goBack{
  width:60px;
  height:60px;
  position: fixed;
  left:0;
  top:0;
  line-height: 60px;
}
#content{
  margin-top: 60px;
  margin-bottom:60px;
}
#header{
  height:60px;
}
</style>



