<template>
  <div id='tmpl'>
    <!--------------------搜索条件-------------------->
    <div class="searchs">
      <input type="text" placeholder="请输入你想要查询的患者名称" v-model="pName"/>
      <button @click="getDataList(searchNum,pName)">搜索</button>
    </div>

    <!----------------------九宫格三个标题----------------->
    <div class="mui-content">
      <ul class="mui-table-view mui-grid-view mui-grid-9">
        <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" @click="getDataList(0,pName)">
            <img :src="icon1"/>
            <div class="mui-media-body">未评估</div>
        </li>
        <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" @click="getDataList(1,pName)">
            <img :src="icon2"/>
            <div class="mui-media-body">允许离院</div>
        </li>
        <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" @click="getDataList(2,pName)">
            <img :src="icon3"/>
            <div class="mui-media-body">不可离院</div>
        </li>
      </ul>
    </div>

    <!------------------患者评估列表---------------------------->
    <div class="opeList" style="height:300px;overflow: scroll">
      <v-loadmore
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded"
        :auto-fill="false"
        ref="loadmore">
        <ul>
          <li v-for="item in proCopyright" :key="item.id">
            <router-link :to="{path:'/outHospitalEvaluationDetails',query:{id:item.id}}">
              <div class="names">{{ item.patientName }}</div>
              <div class="contents">
                <h4>{{ item.operationName }}</h4>
                <p>手术时间：{{ item.operationDate }}</p>
              </div>
            </router-link>
          </li>
        </ul>
      </v-loadmore>
    </div>
  </div>
</template>

<script>
  import {Loadmore} from 'mint-ui';
  export default {
    components:{
      'v-loadmore':Loadmore
    },
    data:function(){
      return {
        //初始化搜索按钮的的值
        searchNum:0,
        icon1:'../../static/imgs/iconIndex/icon-2.png',
        icon2:'../../static/imgs/iconIndex/icon-3.png',
        icon3:'../../static/imgs/iconIndex/icon-4.png',
        dataList:[],
        parentId:0,
        pName:'',//搜索框中病人的名字
        evaluate:0,//离院评估状态 0 未评估（默认） 1 允许出院  2 不允许出院
        page:1,//页数
        size:4,//每页显示行数

        //pageNo:1,
        //pageSize:50,
        proCopyright:[],
        allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
        scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
        totalpage:0,
        loading:false,
        bottomText: ''

      }
    },
    created:function() {
      this.getDataList();
    },
    mounted:function(){
      this.loadPageList();  //初次访问查询列表
    },
    methods:{
      getDataList:function(num,pName){
        if(num == 0){
          this.searchNum = 0;

        }
        if(num == 1){
          this.searchNum = 1;
          this.page = 1//页数
        }
        if(num == 2){
          this.searchNum = 2;
          this.page = 1//页数
        }
        //console.log(this.searchNum,pName);
        //alert(q);
        var url = '/api/com/pad/getOperationManger';
        this.$http.get(url,{params:{
          pName:this.pName,//手术名称
          evaluate:this.searchNum,//标志手术评估状态
          page:this.page,//当前第几页
          size:this.size//每页显示的条数
        }}).then(function(res){
          //alert('fdsa');
          console.log(res);
          if(res.body.zt === 'true'){

            this.proCopyright = res.body.data;
            //console.log(this.$refs.loadmore);
          }
        });
      },
      loadBottom:function() {
        // 上拉加载
        this.more();// 上拉触发的分页查询
        //console.log(this.$refs.loadmore);
        this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
      },
      loadPageList:function (){
        // 查询数据
        this.$http.get('/api/com/pad/getOperationManger?evaluate='+this.searchNum+'&page='+this.page+'&size='+this.size
          ).then(function(res){
          //console.log(res);
          this.proCopyright = res.body.data;
          this.totalpage = Math.ceil(res.body.data/this.size);
          if(this.totalpage == 1){
            this.allLoaded = true;
          }
          this.$nextTick(function () {
            // 是否还有下一页，加个方法判断，没有下一页要禁止上拉
            this.scrollMode = "touch";
            this.isHaveMore();
          });
        })
      },
      more:function (){
        // 分页查询
        if(this.totalpage == 1){
          this.page = 1;
          this.allLoaded = true;
        }else{
          this.page = parseInt(this.page) + 1;
          this.allLoaded = false;
        }

       // console.log(this.pageNo);
        this.$http.get('/api/com/pad/getOperationManger?evaluate='+this.searchNum+'&page='+this.page+'&size='+this.size,
          ).then(function(res){
            //console.log(res);
          this.proCopyright = this.proCopyright.concat(res.body.data);
          console.log(this.proCopyright);
          this.isHaveMore();
        })
      },
      isHaveMore:function(){
        // 是否还有下一页，如果没有就禁止上拉刷新
        //this.allLoaded = false; //true是禁止上拉加载
        if(this.page == this.totalpage){
          this.allLoaded = true;
        }
      }
    }
  }
</script>

<style scoped>
  li img{
    width:40%
  }
  .opeList{
   overflow: scroll;
  }
  .searchs{
    box-sizing: border-box;
    padding:6px;
    /*position:fixed;*/
    /*background-color: #fff;*/
    width:100%;
  }
  button{
    box-sizing: border-box;
    padding:10px 20px;
    border-radius:35px;
    background-color: #26a2ff;
    color:#fff;
  }
  .searchs input{
    width:75%;
    margin:0px;
    border-radius:35px;
  }
ul{
  list-style: none;
  padding:10px 20px;
  margin:0;
}
ul li{
    width:100%;
    height:60px;
  }
.mui-content ul li{
  width:33%;
  height:100px;
  border-bottom:1px solid #ccc;
  box-sizing: border-box;
  padding:10px;
  float:left;
}
  ul .names{
    float:left;
    width:20%;
    height:60px;
  }
  ul .contents{
    float:left;
    width:80%;
    height:60px;
  }
  h4{
    margin:0;
  }
</style>




