import Vue from 'vue'
import Router from 'vue-router'
import outHospitalEvaluation from '@/components/outHospitalEvaluation'
import outHospitalEvaluationDetails from '@/components/outHospitalEvaluationDetails'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'outHospitalEvaluation',
      component: outHospitalEvaluation
    }
    ,{
      path: '/outHospitalEvaluationDetails',
      name: 'outHospitalEvaluationDetails',
      component: outHospitalEvaluationDetails
    }
  ]
})



