<template>
  <div id="tmpl">
    <form action="">
      <ul class="">
        <li class="radioChoice">
          <span>基本生命特征</span>
        </li>
        <li>
          <span>体温：</span>
          <input type="text" v-model="dataList.templature"/>
        </li>
        <li>
          <span>脉搏：</span>
          <input type="text" v-model="dataList.pulse"/>
        </li>
        <li>
          <span>呼吸：</span>
          <input type="text" v-model="dataList.breath"/>
        </li>
        <li>
          <input name="lifeStyle" type="radio" value="0" checked v-model="dataList.life_status"/>
          <span>正常</span>
          <input name="lifeStyle" type="radio" value="1" v-model="dataList.life_status"/>
          <span>异常</span>
        </li>
        <li>
          <span>活动能力：</span>
          <input type="text" v-model="dataList.action"/>
        </li>
        <li class="icons">
          <span>疼痛指数：</span>
          <ul class="faceList">
            <li v-for="(item,pain) in iconList" :key="item.id" @click="getIndex(pain)">
              <img :src="item"/>
            </li>
          </ul>
        </li>
        <li class="radioChoice">
          <span>术后恶心呕吐：</span>
          <input name="nausea" type="radio" value="0" checked v-model="dataList.vomit"/>
          <span>是</span>
          <input name="nausea" type="radio" value="1" v-model="dataList.vomit"/>
          <span>否</span>
        </li>
        <li class="radioChoice">
          <span>切口出血：</span>
          <input name="blood" type="radio" value="0" checked v-model="dataList.bleed"/>
          <span>是</span>
          <input name="blood" type="radio" value="1" v-model="dataList.bleed"/>
          <span>否</span>
        </li>
        <li class="radioChoice">
          <span>准予离院：</span>
          <input name="goAway" type="radio" value="0" checked v-model="dataList.outHospital"/>
          <span>是</span>
          <input name="goAway" type="radio" value="1" v-model="dataList.outHospital"/>
          <span>否</span>
        </li>
        <li>
          <span>评估人：</span>
          <input type="text" v-model="dataList.evaluateName"/>
        </li>
        <li>
          <span>其他：</span>
          <input type="text" v-model="dataList.other"/>
        </li>
      </ul>
    </form>
    <button @click="saveSubtype(1)">允许离院</button>
    <button @click="saveSubtype(2)">不可离院</button>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    data:function(){
      return {
        iconList:[
          '../../static/imgs/iconFace/icon-a.png',
          '../../static/imgs/iconFace/icon-1.png',
          '../../static/imgs/iconFace/icon-g.png',
          '../../static/imgs/iconFace/icon-e.png',
          '../../static/imgs/iconFace/icon-i.png',
          '../../static/imgs/iconFace/icon-j.png'
        ],
        //接收页面传递过来的ID值
        mangerId:0,
        //保存接收的数据(初始化数据也在这里)
        dataList:[{
          //体温
          templature:0,
          //脉搏
          pulse:'',
          //呼吸
          breath:'',
          //正常或不
          life_status:0,
          //活动能力
          action:'',
          //疼痛指数
          pain:0,
          //术后恶心或不
          vomit:0,
          //切口出血或不
          bleed:0,
          //准予离院或不
          outHospital:0,
          //评估人
          evaluateName:'',
          //其他
          other:''
        }]
      }
    },
    created:function(){
      this.getItemData();
    },
    methods:{
      //-----------点击获取当前人脸的index值----------------
      getIndex:function(pain){
        //console.log(pain);
        this.dataList.pain = pain;
      },
      //---------前一个页面传递过来的ID值 以及带过来的数据--------------
      getItemData:function(){
        this.mangerId = this.$route.query.id;
        var url = '/api/com/pad/getOutHospitalEvaluate';
        this.$http.get(url,{
          params:{mangerId:this.mangerId}
        }).then(function(res){
          if(res.body.zt === 'true'){
              this.dataList = res.body.data;
            //console.log(res);
            //console.log(this.mangerId);
          }
        });
      },


      //---------保存用户填写的各种信息资料---------------
      saveSubtype:function(clickNum){
        //this.subtype = 1;
        //var subtype = this.subtype;
        var managerId = this.mangerId;
        //console.log(managerId);
        var url = '/api/com/pad/submitOutHospitalEvaluate';
        this.$http.get(url,{
          params:{
            subType:clickNum,
            ManagerId:managerId,
            templature:this.dataList.templature,
            pulse:this.dataList.pulse,
            breath:this.dataList.breath,
            life_status:this.dataList.life_status,
            action:this.dataList.action,
            pain:this.dataList.pain,
            vomit:this.dataList.vomit,
            bleed:this.dataList.bleed,
            outHospital:this.dataList.outHospital,
            evaluateName:this.dataList.evaluateName,
            other:this.dataList.other
          }}).then(function(res){
          if(res.body.zt === 'true'){
              Toast('提交成功');
          }
        });
      }
    }
  }
</script>

<style scoped>
  button{
    box-sizing: border-box;
    padding:8px 20px;
    border-radius: 35px;
    background-color: #26a2ff;
    color:#fff;
  }
ul{
  box-sizing: border-box;
  padding:0px 0px 0px 10px;
  margin-top:10px;
  list-style: none;
  text-align: left;
}
ul li{
  box-sizing: border-box;
  width:100%;
  height:70px;
  padding:10px;
  text-align: left;
}
.icons{
  height:100px;
  text-align: left;
}
.radioChoice{
  height:50px;
  text-align: left;
}
.faceList{
  height:50px;
  text-align: left;
}
.faceList li{
  float:left;
  width:16%;
  text-align: left;
}
.faceList li img{
  width:100%;
  text-align: left;
}
</style>



